/**退钟 预结钟 */
<template>
  <div class="content">
    <section class="itemContent">
      <van-swipe-cell v-for="(item,index) of this.getRespone.clockJson" :key="index">
        <div class="item">
          <div class="selectBox" @click="selcetItme(item.id)">
            <van-icon
              v-show="currentId != item.id"
              class="vanIco"
              name="checked"
              size="20"
              color="#DCDCDC"
            />
            <van-icon
              v-show="currentId == item.id"
              class="vanIco"
              name="checked"
              size="20"
              color="#0867E9"
            />
          </div>
          <img :src="item.imgSrc" />
          <div class="rightDiv">
            <p>{{item.name}}</p>
            <p>时长：{{item.time}}（分钟）</p>
            <p>价格：￥{{item.price}}</p>
          </div>
        </div>
        <template slot="right">
          <div class="btn">删除</div>
        </template>
      </van-swipe-cell>
    </section>
    <section class="bottomLine">
      <div class="btn">重置</div>
      <div class="btn" @click="submitBtn">确认</div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentId: this.currentId
    };
  },
  methods: {
    selcetItme(id) {
      this.currentId = id;
    },
    submitBtn() {
      this.common_vant.openToast("起钟成功", "800");
      setTimeout(() => {
        this.$router.go(-1);
      }, 1000);
    }
  }
};
</script>

<style lang="scss" scoped>
.itemContent {
  @include fc(center);
  @include wh(100%, auto);
  padding: 0rem 0.8rem;
  .item {
    @include fr(flex-start);
    @include wh(100%, auto);
    padding: 1.2rem 0.8rem;
    border-bottom: 0.02rem solid $F5;
    .vanIco {
      margin-right: 0.5rem;
    }
    img {
      @include wh(3.8rem, 3.8rem);
      border-radius: 5rem;
      border: 0.02rem solid $Tline;
    }
    .rightDiv {
      @include sc(0.75rem, $Tline);
      margin-left: 0.8rem;
      text-align: left;
      p:first-child {
        @include sc(0.9rem, $fColor);
        font-weight: bold;
      }
      p:nth-child(2) {
        margin: 0.15rem 0rem;
      }
    }
  }
  .btn {
    @include wh(auto, 100%);
    @include fc(center);
    padding: 1.2rem 0.8rem;
    background-color: red;
    color: $fc;
  }
}
/deep/.van-swipe-cell {
  @include wh(100%, auto);
}

.bottomLine {
  @include fr(space-between);
  @include wh(100%, auto);
  padding: 0.8rem 0.5rem;
  position: absolute;
  bottom: 2rem;
  .btn {
    @include wh(50%, auto);
    padding: 0.5rem;
    border-radius: 0.5rem;
    margin: 0.8rem;
  }
  .btn:first-child {
    @include sc(0.85rem,#589ffa);
    border: 0.1rem solid #589ffa;
  }
  .btn:last-child {
    @include sc(0.85rem, $fc);
    background-color: #589ffa;
    background-image: linear-gradient(
      180deg,
      #589ffa 0%,
      #6e95f3 50%,
      #7a90ef 100%
    );
  }
}
</style>